import axios from 'axios';
// jselementUI
import { Message } from 'element-ui';

import router from "@/router";

// axios
axios.interceptors.response.use(success => {
    // 500
    // success.status,
    if (success.status && success.status == 200 && success.data.status == 500) {
        Message.error({message: success.data.msg, duration:2000, showClose:true})
        return;
    }
    if (success.data.msg) {
        Message.success({message: success.data.msg, duration:2000, showClose:true})
    }
    return success.data;
}, error => {
    if (error.response.status == 504 || error.response.status == 404) {
        Message.error({message: '(  )', duration:2000, showClose:true})
    } else if (error.response.status == 403) {
        Message.error({message: '', duration:2000, showClose:true})
    } else if (error.response.status == 401) {
        Message.error({message: '', duration:2000, showClose:true});
        router.replace('/');
    } else {
        if (error.response.data.msg) {
            Message.error({message: error.response.data.msg, duration:2000})
        } else {
            Message.error({message: '', duration:2000, showClose:true})
        }
    }
    return;
})

let base = '';
/*
  SpringBoot-securityKeyValue
  json
  axiosjson
*/
export const postKeyValueRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params,
        transformRequest: [function (data) {
            let ret = '';
            for (let i in data) {
                ret += encodeURIComponent(i) + '=' + encodeURIComponent(data[i]) + '&'
            }
            return ret;
        }],
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}
// restful,post delete put get,export-

// 
export const postRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params
    })
}
// 
export const putRequest = (url, params) => {
    return axios({
        method: 'put',
        url: `${base}${url}`,
        data: params
    })
}
// urlencoded
export const getRequest = (url, params) => {
    return axios({
        method: 'get',
        url: `${base}${url}`,
        params: params
    })
}
// urlencoded
export const deleteRequest = (url, params) => {
    return axios({
        method: 'delete',
        url: `${base}${url}`,
        params: params
    })
}
// 
export const deleteRequestData = (url, params) => {
    return axios({
        method: 'delete',
        url: `${base}${url}`,
        data: params
    })
}