<template>
    <div>
        <el-form :rules="rules"
                 :model="loginForm"
                 class="loginContainer"
                 ref="loginForm"
                 v-loading="loading">
            <h3 class="loginTitle"></h3>
            <el-form-item prop="username">
                <el-input size="normal" type="text" v-model="loginForm.username" auto-complete="off"
                          placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="password" auto-complete="off">
                <el-input size="normal" type="password" v-model="loginForm.password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="code" auto-complete="off">
                <el-input
                        size="normal"
                        type="text"
                        v-model="loginForm.code"
                        @keydown.enter.native="submitLogin"
                        placeholder=""
                        style="width: 250px">
                </el-input>
                <img :src="verifyImg" @click="flushCode" title="" style="cursor: pointer">
            </el-form-item>
            <el-button size="normal" type="primary" style="width: 100%" @click="submitLogin()"></el-button>
        </el-form>
    </div>
</template>

<script>
  // import {postKeyValueRequest} from "@/utils/api";
  export default {
    name: "Login",
    data() {
      return {
        verifyImg: '/verify',
        loading: false,
        loginForm: {
          username: 'admin',
          password: '123',
          code: ''
        },
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
          ],
          // code: [
          //   {required: true, message: '', trigger: 'blur'},
          // ],
        },
      }
    },
    mounted() {
      this.open();
    },
    /**/
    methods: {
      open() {
        this.$alert('<strong></strong>', '', {
          dangerouslyUseHTMLString: true
        })
      },
      // 
      flushCode() {
        this.verifyImg = '/verify?time=' + new Date();
      },
      submitLogin() {
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {
              this.loading = false;
              if (resp) {
                this.$store.commit('initHr', resp.obj);
                window.sessionStorage.setItem('user', JSON.stringify(resp.obj));
                // router.beforeEachnextpath
                let path = this.$route.query.redirect;
                // replace,push,
                // $router main.jsrouter
                this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
              } else {
                this.flushCode();
              }
            })
          }
        })
      }
    }
  }
</script>

<style>
    /**/
    .loginContainer {
        border-radius: 15px; /**/
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 5px 35px 25px 35px;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    /**/
    .loginTitle {
        font-size: 24px;
        font-family: ;
        text-align: center;
        color: #505458;
    }

    .el-form-item__content {
        display: flex;
        align-items: center;
    }
</style>